<template>
	<view class="l_index">


		<view class="icontext" style=" position: fixed; top: 17upx; right: 30upx; z-index: 30;">
			&#xe63d;<navigator url="../comment/comment?card_id=main" style="font-size: 24upx;"> 评论</navigator>
		</view>

		<scroll-view id="tab-bar" class="swiper-tab" scroll-x :scroll-left="scrollLeft">
			<block v-for="(tab,index) in tabs" :key="tab.id">
				<view :class="['swiper-tab-list',currentTab==index ? 'tap_on' : '']" :id="tab.id" :data-current="index" @tap="swichNav">{{tab.name}}</view>
			</block>
		</scroll-view>
		<swiper :current="currentTab" class="tab-swiper-box border-style" duration="300" @change="bindChange">
			<swiper-item>
				<beixuanview> </beixuanview>
			</swiper-item>
			<swiper-item>
				<collectionview> </collectionview>
			</swiper-item>
			<swiper-item >
				<handleview card_id="main" > </handleview>
			</swiper-item>


		</swiper>
	</view>
</template>
<script>
	import beixuanview from '../../components/beixuan.vue'
	import collectionview from '../../components/collection.vue'
	import handleview from '../../components/handle-view.vue'
	export default {
		data() {
			return {


				scrollLeft: 0,
				isClickChange: false,
				currentTab: 0,
				tabs: [{
					name: '备选',
					id: 'beixuan'
				}, {
					name: '收藏',
					id: 'pinglun'
				}, {
					name: '汇总',
					id: 'dashuju'
				}],

				collect_list: [],
				luyouData: null,




			}
		},
		components: {
			collectionview,
			beixuanview,
			handleview,
		},

		onLoad: async function() {

		},


		onUnload: function() {
			this.scrollLeft = 0,
				this.isClickChange = false,
				this.currentTab = 0;
		},
		methods: {






			bindChange: async function(e) {
				let index = e.target.current;
				if (this.isClickChange) {
					this.currentTab = index;
					this.isClickChange = false;
					return;
				}
				let tabBar = await this.getWidth("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft;

				let width = 0;

				for (let i = 0; i < index; i++) {
					let result = await this.getWidth(this.tabs[i].id);
					width += result.width;
				}

				let winWidth = uni.getSystemInfoSync().windowWidth,
					nowElement = await this.getWidth(this.tabs[index].id),
					nowWidth = nowElement.width;

				if (width + nowWidth - tabBarScrollLeft > winWidth) {
					this.scrollLeft = width + nowWidth - winWidth;
				}
				if (width < tabBarScrollLeft) {
					this.scrollLeft = width;
				}
				this.isClickChange = false;
				this.currentTab = index; //一旦访问data就会出问题
			},
			getWidth: function(id) { //得到元素的宽高
				return new Promise((res, rej) => {
					uni.createSelectorQuery().select("#" + id).fields({
						size: true,
						scrollOffset: true
					}, (data) => {
						if (id === 'tab-bar') {
							console.log("id=", id, "数据:", data)
						}
						res(data);
					}).exec();
				})
			},
			swichNav: async function(e) { //点击tab-bar
				if (this.currentTab === e.target.dataset.current) {
					return false;
				} else {
					let tabBar = await this.getWidth("tab-bar"),
						tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
					this.scrollLeft = tabBarScrollLeft;
					this.isClickChange = true;
					this.currentTab = e.target.dataset.current
				}
			},




		}
	}
</script>

<style>
	.l_index {
		flex: 1;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		overflow: hidden;
		height: 1000upx;

	}


	.swiper-tab {
		width: 750upx;
		white-space: nowrap;
		line-height: 54upx;
		height: 64upx;
		position: fixed;
		top: 0upx;
		z-index: 10;
		/* background-color: #EFEFEF; */
	}


	.swiper-tab-list {
		font-size: 30upx;
		width: 150upx;
		display: inline-block;
		text-align: center;
		color: #777777;
	}

	.tap_on {
		color: 	RoyalBlue;
		border-bottom: 10upx solid 	RoyalBlue;
	}

	.tab-swiper-box {
		flex: 1;
		width: 750upx;
		height: 100%;
		position: fixed;
		top: 64upx;
		z-index: 10;
		/* background-color: #EFEFEF; */
	}






	::-webkit-scrollbar {
		width: 0;
		height: 0;
		color: transparent;
	}
</style>



